.product-count {
  box-sizing: border-box;
  display: flex;
  border: solid 2px $black;
  width: 74%;
  max-width: 114px;
  min-width: 81px;
  font-weight: bold;
  height: 34px;
  font-size: 16px;
  line-height: 18px;

  @media (min-width: $mobil) {
    height: 40px;
    font-size: 20px;
    line-height: 23px;
    max-width: 200px;
  }
}

.product-count__num {
  width: 20%;
  text-align: center;
  margin: auto;
}

.product-count__sign {
  width: 40%;
}

.product-count__sign {
  position: relative;
  &::after {
    position: absolute;
    content: "";
    background-repeat: no-repeat;
    background-position: center;
    display: block;
    width: 12px;
    left: calc(50% - 6px);
  }

  &--minus::after {
    background-image: url("../img/svg/minus.svg");
    bottom: calc(50% - 1px);
    height: 2px;
  }
  &--plus::after {
    background-image: url("../img/svg/plus.svg");
    height: 12px;
    bottom: calc(50% - 6px);
  }

  @media (min-width: $mobil) {
    &::after {
      width: 16px;
      left: calc(50% - 8px);
    }
    &--minus::after {
      bottom: calc(50% - 1px);
      height: 2px;
    }
    &--plus::after {
      height: 16px;
      bottom: calc(50% - 8px);
    }
  }
}


