.page-cart {
  display: flex;
  flex-direction: column;
  justify-content: center;


  padding: 40px 16px;
  @media (min-width: $mobil) {
    padding: 50px 0 70px;
  }

  @media (min-width: 1970px) {
    padding: 50px 405px 70px;
  }
}

.page-cart__wrapper {
  margin: auto;
  position: relative;
  width: 100%;
  min-width: 288px;
  max-width: 505px;

  @media (min-width: $mobil) {
    min-width: $mobil;
  }

  @media (min-width: 1970px) {
    min-width: 100%;
  }
}
