.package {
  position: relative;
  &::after {
    right: 2px;
    bottom: 29px;
    position: absolute;
    content: "";
    background-image: url("../img/svg/arrow-down.svg");
    background-repeat: no-repeat;
    background-position: center;
    display: block;
    width: 14px;
    height: 7px;
    cursor: pointer;
  }
  &--up {
    &::after {
    transform: rotate(180deg);
  }}
}

.package__option {
  padding: 20px 0;
}
