.result-desktop {
  width: 100%;
  display: none;

@media (min-width: $mobil) {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    bottom: 0;
    font-size: 40px;
    line-height: 46px;
    padding: 30px;
    font-weight: bold;
    background-color: rgba($color: #000000, $alpha: 0.5);
    height: 136px;
    color: $black;
  }
}
