.logo-nav {
  display: none;
  margin-bottom: 41px;
  @media (min-width: $mobil) {
    display: flex;
    justify-content: space-between;
  }
}

.logo-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items:flex-end
}

.logo-nav__item a {
  text-decoration: none;
  text-transform: uppercase;
  color: $gray;

  &:hover, &:focus {
    color: $black;
  }
}

.logo-nav__logo {
  width: 180px;
  height: 50px;
}

.logo-nav__item {
  margin: 0 30px;

  &:first-child {
    margin-left: 0;
  }
  &:last-child {
    margin-right: 0;
  }
}
