.breadcrumbs {
  width: 100%;
  margin-bottom: 32px;
  @media (min-width: $mobil) {
    margin-bottom: 50px;
  }
}

.breadcrumbs__list {
  font-style: 18px;
  line-height: 21px;
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.breadcrumbs__item {
  margin: 0;
  margin-right: 26px;

  & a {
    text-decoration: none;
    color: $gray;
  }

  &:not(:last-child) {
    &::after {
      position: absolute;
      content: '/';
      padding-left: 10px;
    }
  }
}

.breadcrumbs__item:last-child a {
  color: $black;
}
