@mixin hidden-mix {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

@mixin Helvetica($size, $height, $color: inherit, $weight: normal) {
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: $weight;
  font-size: #{$size}px;
  line-height: #{$height}px;
  color: $color;
  font-style: normal;
}
