.reg-form {
  width: 100%;
  font-size: 16px;
  line-height: 120%;
  margin-bottom: 40px;

  @media (min-width: $mobil) {
    margin-bottom: 70px;
  }
}

.form-label {
  font-size: 12px;
  line-height: 14px;
  color: $gray;

  &--hidden {
    opacity: 0;
  }

  &--error {
    color: $red;
  }
}

.reg-form__input {
  width: 100%;
  border: none;
  border-bottom: 1px solid $gray;
  padding: 4px 0 9px;
  margin-bottom: 16px;

  &:focus {
    outline: none;
  }

  @media (min-width: $mobil) {
    margin-bottom: 12px;
  }
}

.reg-form__input[placeholder] {
  color: $gray;
}

.reg-form__input--error[placeholder] {
  color: $red
}
.reg-form__name-tel {
  width: 100%;

  @media (min-width: $mobil) {
    display: flex;
    justify-content: space-between;
  }
}

.reg-form__textarea {
  height: 22px;
  width: 100%;
  border: none;
  border-bottom: 1px solid $gray;
  font-size: 16px;
  line-height: 19px;
}

.reg-form__name, .reg-form__tel {
  width: 100%;

  @media (min-width: $mobil) {
    width: 45%;
  }

}
