@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/HelveticaRegular.woff") format("woff2");
}
@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/HelveticaBold.woff") format("woff2");
}
.visually-hidden {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

body {
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: inherit;
  font-style: normal;
}

.page-cart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 40px 16px;
}
@media (min-width: 1160px) {
  .page-cart {
    padding: 50px 0 70px;
  }
}
@media (min-width: 1970px) {
  .page-cart {
    padding: 50px 405px 70px;
  }
}

.page-cart__wrapper {
  margin: auto;
  position: relative;
  width: 100%;
  min-width: 288px;
  max-width: 505px;
}
@media (min-width: 1160px) {
  .page-cart__wrapper {
    min-width: 1160px;
  }
}
@media (min-width: 1970px) {
  .page-cart__wrapper {
    min-width: 100%;
  }
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
@media (min-width: 1160px) {
  .form {
    width: 43.5%;
    border: none;
  }
}

.form__title {
  margin: 0;
  margin-bottom: 12px;
}
@media (min-width: 1160px) {
  .form__title {
    margin-bottom: 15px;
  }
}

.map-section {
  min-width: 288px;
  width: 100%;
  height: 200px;
  background-color: #9F9F9F;
  margin-bottom: 16px;
}
@media (min-width: 1160px) {
  .map-section {
    width: 48%;
    height: 700px;
    position: absolute;
    right: 0;
    top: 100px;
  }
}

.reg-form {
  width: 100%;
  font-size: 16px;
  line-height: 120%;
  margin-bottom: 40px;
}
@media (min-width: 1160px) {
  .reg-form {
    margin-bottom: 70px;
  }
}

.form-label {
  font-size: 12px;
  line-height: 14px;
  color: #9F9F9F;
}
.form-label--hidden {
  opacity: 0;
}
.form-label--error {
  color: #D80909;
}

.reg-form__input {
  width: 100%;
  border: none;
  border-bottom: 1px solid #9F9F9F;
  padding: 4px 0 9px;
  margin-bottom: 16px;
}
.reg-form__input:focus {
  outline: none;
}
@media (min-width: 1160px) {
  .reg-form__input {
    margin-bottom: 12px;
  }
}

.reg-form__input[placeholder] {
  color: #9F9F9F;
}

.reg-form__input--error[placeholder] {
  color: #D80909;
}

.reg-form__name-tel {
  width: 100%;
}
@media (min-width: 1160px) {
  .reg-form__name-tel {
    display: flex;
    justify-content: space-between;
  }
}

.reg-form__textarea {
  height: 22px;
  width: 100%;
  border: none;
  border-bottom: 1px solid #9F9F9F;
  font-size: 16px;
  line-height: 19px;
}

.reg-form__name, .reg-form__tel {
  width: 100%;
}
@media (min-width: 1160px) {
  .reg-form__name, .reg-form__tel {
    width: 45%;
  }
}

.package {
  position: relative;
}
.package::after {
  right: 2px;
  bottom: 29px;
  position: absolute;
  content: "";
  background-image: url("../img/svg/arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center;
  display: block;
  width: 14px;
  height: 7px;
  cursor: pointer;
}
.package--up::after {
  transform: rotate(180deg);
}

.package__option {
  padding: 20px 0;
}

.products {
  margin: auto;
  width: 100%;
  margin-bottom: 24px;
}
@media (min-width: 1160px) {
  .products {
    margin-bottom: 0;
  }
}

.products__list {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.products__title {
  font-size: 26px;
  line-height: 30px;
  margin: 0;
  margin-bottom: 7px;
}
@media (min-width: 1160px) {
  .products__title {
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 5px;
  }
}

.products__item {
  padding: 16px 0;
  border-bottom: 1px solid #DEDEDE;
  width: 100%;
}
@media (min-width: 1160px) {
  .products__item {
    padding: 25px 0;
  }
}

.product-card {
  display: flex;
  max-width: 505px;
}
.product-card button {
  cursor: pointer;
  background-color: inherit;
  border: none;
}
@media (min-width: 1160px) {
  .product-card {
    max-width: 100%;
  }
}

.product-card__image {
  margin-top: auto;
  margin-bottom: auto;
  width: 80px;
  height: 80px;
}
@media (min-width: 1160px) {
  .product-card__image {
    width: 150px;
    height: 150px;
  }
}

.product-card__info {
  box-sizing: border-box;
  width: 41%;
  min-width: 130px;
  padding: 0 12px;
}
@media (min-width: 1160px) {
  .product-card__info {
    width: 228px;
    padding: 0 20px;
  }
}

.product-card__description {
  display: none;
}
@media (min-width: 1160px) {
  .product-card__description {
    display: block;
    color: #9F9F9F;
  }
}

.product-card__delete {
  color: #9F9F9F;
}
.product-card__delete:hover, .product-card__delete:focus {
  color: #000000;
}

.product-card__end {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 28%;
}
@media (min-width: 1160px) {
  .product-card__end {
    width: 81px;
    padding: 0 25px;
  }
}

.product-card__price {
  text-align: center;
}

.product-count {
  box-sizing: border-box;
  display: flex;
  border: solid 2px #000000;
  width: 74%;
  max-width: 114px;
  min-width: 81px;
  font-weight: bold;
  height: 34px;
  font-size: 16px;
  line-height: 18px;
}
@media (min-width: 1160px) {
  .product-count {
    height: 40px;
    font-size: 20px;
    line-height: 23px;
    max-width: 200px;
  }
}

.product-count__num {
  width: 20%;
  text-align: center;
  margin: auto;
}

.product-count__sign {
  width: 40%;
}

.product-count__sign {
  position: relative;
}
.product-count__sign::after {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  display: block;
  width: 12px;
  left: calc(50% - 6px);
}
.product-count__sign--minus::after {
  background-image: url("../img/svg/minus.svg");
  bottom: calc(50% - 1px);
  height: 2px;
}
.product-count__sign--plus::after {
  background-image: url("../img/svg/plus.svg");
  height: 12px;
  bottom: calc(50% - 6px);
}
@media (min-width: 1160px) {
  .product-count__sign::after {
    width: 16px;
    left: calc(50% - 8px);
  }
  .product-count__sign--minus::after {
    bottom: calc(50% - 1px);
    height: 2px;
  }
  .product-count__sign--plus::after {
    height: 16px;
    bottom: calc(50% - 8px);
  }
}

.submit-btn {
  background-color: #ffffff;
  width: 100%;
  height: 60px;
  cursor: pointer;
}
.submit-btn:hover, .submit-btn:focus {
  background-color: #000000;
  color: #ffffff;
}
@media (min-width: 1160px) {
  .submit-btn {
    height: 70px;
  }
}

.result-mobile {
  display: flex;
  justify-content: space-between;
  font-size: 28px;
  line-height: 30px;
  height: 30px;
  font-weight: bold;
  margin-bottom: 24px;
}
@media (min-width: 1160px) {
  .result-mobile {
    display: none;
  }
}

.result-mobile p {
  margin: 0;
}

.login {
  font-size: 16px;
  line-height: 19px;
  margin-bottom: 32px;
}
@media (min-width: 1160px) {
  .login {
    margin-bottom: 30px;
  }
}

.login__link {
  color: #9F9F9F;
}
.login__link:hover, .login__link:focus {
  color: #000000;
}

.login__text {
  margin: 0;
}

.breadcrumbs {
  width: 100%;
  margin-bottom: 32px;
}
@media (min-width: 1160px) {
  .breadcrumbs {
    margin-bottom: 50px;
  }
}

.breadcrumbs__list {
  font-style: 18px;
  line-height: 21px;
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.breadcrumbs__item {
  margin: 0;
  margin-right: 26px;
}
.breadcrumbs__item a {
  text-decoration: none;
  color: #9F9F9F;
}
.breadcrumbs__item:not(:last-child)::after {
  position: absolute;
  content: "/";
  padding-left: 10px;
}

.breadcrumbs__item:last-child a {
  color: #000000;
}

.result-desktop {
  width: 100%;
  display: none;
}
@media (min-width: 1160px) {
  .result-desktop {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    bottom: 0;
    font-size: 40px;
    line-height: 46px;
    padding: 30px;
    font-weight: bold;
    background-color: rgba(0, 0, 0, 0.5);
    height: 136px;
    color: #000000;
  }
}

.logo-nav {
  display: none;
  margin-bottom: 41px;
}
@media (min-width: 1160px) {
  .logo-nav {
    display: flex;
    justify-content: space-between;
  }
}

.logo-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.logo-nav__item a {
  text-decoration: none;
  text-transform: uppercase;
  color: #9F9F9F;
}
.logo-nav__item a:hover, .logo-nav__item a:focus {
  color: #000000;
}

.logo-nav__logo {
  width: 180px;
  height: 50px;
}

.logo-nav__item {
  margin: 0 30px;
}
.logo-nav__item:first-child {
  margin-left: 0;
}
.logo-nav__item:last-child {
  margin-right: 0;
}