.products {
  margin: auto;
  width: 100%;
  margin-bottom: 24px;

  @media (min-width: $mobil) {
    margin-bottom: 0;
  }
}

.products__list {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.products__title {
  font-size: 26px;
  line-height: 30px;
  margin: 0;
  margin-bottom: 7px;

  @media (min-width: $mobil) {
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 5px;
  }
}
