
.products__item {
  padding: 16px 0;
  border-bottom: 1px solid #DEDEDE;
  width: 100%;

  @media (min-width: $mobil) {
    padding: 25px 0;
  }
}

.product-card {
  display: flex;
  max-width: 505px;

  button {
    cursor: pointer;
    background-color: inherit;
    border: none;
  }

  @media (min-width: $mobil) {
    max-width: 100%;
  }
}

.product-card__image {
  margin-top: auto;
  margin-bottom: auto;

  width: 80px;
  height: 80px;

  @media (min-width: $mobil) {
    width: 150px;
    height: 150px;
  }

}

.product-card__info {
  box-sizing: border-box;
  width: 41%;
  min-width: 130px;
  padding: 0 12px;

  @media (min-width: $mobil) {
    width: 228px;
    padding: 0 20px;
  }
}

.product-card__description {
  display: none;

  @media (min-width: $mobil) {
    display: block;
    color: $gray;
  }
}

.product-card__delete {
  color: $gray;
  &:hover, &:focus {
    color: $black;
  }
}

.product-card__end {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 28%;
  @media (min-width: $mobil) {
    width: 81px;
    padding: 0 25px;
  }
}

.product-card__price {
  text-align: center;
}
